<script>
import SuccessAlert from "@/components/SuccessAlert";
import { required, helpers, numeric } from "vuelidate/lib/validators";

const alpha = helpers.regex("alpha", /^[–ê-–Ø–∞-—è\s]+$'*$/);
const tel = helpers.regex("tel", /^\+?[7][-\(]?\d{3}\)?-?\d{3}-?\d{2}-?\d{2}$/);
const maxDate = date =>
  helpers.withParams(
    { type: "maxDate", value: date },
    value => !helpers.req(value) || date >= value
  );

const dateNow = new Date().toISOString().substring(0, 10);

let tomorrow = new Date();
tomorrow.setDate(tomorrow.getDate() + 1);
tomorrow = tomorrow.toISOString().substring(0, 10);

export default {
  name: "App",
  data() {
    return {
      step: 1,
      successReg: "",
      formFields: {
        lastName: "",
        name: "",
        middleName: "",
        birthday: "",
        telephone: "",
        gender: "",
        —ÅlientGroup: [],
        doctor: "",
        sms: "",
        index: "",
        country: "",
        region: "",
        city: "",
        street: "",
        house: "",
        document: "",
        docSeries: "",
        docNumber: "",
        docOrigin: "",
        docDate: ""
      }
    };
  },
  validations: {
    formFields: {
      lastName: {
        required,
        alpha
      },
      name: {
        required,
        alpha
      },
      middleName: {
        alpha
      },
      birthday: {
        required,
        maxDate: maxDate(dateNow)
      },
      telephone: {
        required,
        tel
      },
      gender: {},
      —ÅlientGroup: {
        required
      },
      doctor: {},
      sms: {},
      index: {
        numeric
      },
      country: {
        alpha
      },
      region: {
        alpha
      },
      city: {
        required,
        alpha
      },
      street: {
        alpha
      },
      house: {},
      document: {
        required
      },
      docSeries: {
        numeric
      },
      docNumber: {
        numeric
      },
      docOrigin: {},
      docDate: {
        required,
        maxDate: maxDate(tomorrow)
      }
    }
  },
  methods: {
    checkStep() {
      let valid = true;
      let actualFields = this.actualFormFieldKeys();

      for (let i = 0; i < actualFields.length; i++) {
        this.$v.formFields[actualFields[i]].$touch();

        if (this.$v.formFields[actualFields[i]].$invalid && valid != false) {
          valid = false;
        }
      }

      return valid;
    },
    nextStep() {
      if (this.checkStep() == false) return false;

      let steps = this.$el.querySelectorAll(".step").length;
      if (this.step < steps) this.step++;
    },
    prevStep() {
      if (this.step > 1) this.step--;
    },
    registerClient() {
      this.$v.$touch();

      if (!this.$v.$invalid) {
        this.step = 0;
        this.successReg = true;
      }
    },
    actualFormFieldKeys() {
      let actualQuantityFields = this.$el
        .querySelectorAll(".step")
        [this.step - 1].querySelectorAll("label").length;
      let firstActualFields = this.prevQuantityFields();

      let fields = Object.keys(this.formFields).slice(
        firstActualFields,
        firstActualFields + actualQuantityFields
      );

      return fields;
    },
    prevQuantityFields() {
      let prevStepsFields = 0;

      for (let i = 0; i < this.step - 1; i++) {
        prevStepsFields += this.$el
          .querySelectorAll(".step")
          [i].querySelectorAll("label").length;
      }

      console.log(this.$v.formFields.birthday.$model);

      return prevStepsFields;
    }
  },
  components: {
    SuccessAlert
  }
};
</script>


